import { useState, useEffect } from 'react';
import axios from 'axios';
import { PHOTOS_URL } from './config';
// 'https://stocksnap.io/api/search-photos/nature/trending/asc/1';

const usePhotosApi = (
  category,
  currentPage = 1,
  sorting = 'trending',
  sortingBy = 'asc',
) => {
  const [photos, setPhotos] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [isError, setIsError] = useState(false);
  const [nextPage, setNextPage] = useState(currentPage + 1);

  useEffect(() => {
    const fetchPhotos = async page => {
      // console.log('fetchPhotos fetching...');
      setIsLoading(true);
      setIsError(false);

      try {
        // await new Promise(resolve => setTimeout(resolve, 2000));
        const {
          data: { results, nextPage },
        } = await axios.get(
          `${PHOTOS_URL}/${category}/${sorting}/${sortingBy}/${page}`,
        );

        page === 1
          ? setPhotos([...results])
          : setPhotos(photos => [...photos, ...results]);

        setNextPage(nextPage);
      } catch (e) {
        console.log('Error: ', e);
        setIsError(true);
      } finally {
        // console.log('fetchPhotos fetched!!!');
        setIsLoading(false);
        window.dispatchEvent(new CustomEvent('scroll'));
      }
    };

    fetchPhotos(currentPage);
  }, [category, sorting, sortingBy, currentPage]);

  return [photos, isLoading, isError, nextPage];
};

export default usePhotosApi;
